          />
          <button class="btn btn-outline-primary btn-lg" type="submit">
            <i class="bi-geo-alt-fill me-2" /> Suivre
          </button>
        </div>
        <div class="text-center text-warning" v-if="errorMessage">
          {{ errorMessage }}
          <hr class="m-1" />
          <button class="btn btn-block btn-warning" @click="tray_again" type="button">Réessayer</button>
        </div>
        <loading-compoenent v-else-if="isLoading" />
        <div class="card card-body mt-4" v-if="request">
          <!--  form -->
          <div class="row">
            <div class="col">
              <div class="form-group my-2">
                <label>Téléphone de Distributeur *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.userPhone || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Type de Paquet *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.packageType || "/" }}</p>
              </div>
            </div>
          </div>
          <div class="form-group my-2">
            <label>Description *:</label>
            <p class="alert alert-secondary px-2 py-1">{{request.description || "/" }}</p>
          </div>
          <div class="row">
            <div class="col">
              <div class="form-group my-2">
                <label>Poids (kg) *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.weight || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Largeur (cm) *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.width || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Longueur (cm) *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.length || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Hauteur (cm) *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.height || "/" }}</p>
              </div>
            </div>
          </div>
          <hr class="my-1" />
          <div class="row">
            <div class="col">
              <div class="form-group my-2">
                <label>Wilaya -départ- *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.wilayaFrom || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Baladia -départ- *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.baladiaFrom || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Position GPS -départ- *:</label>
                <p class="alert alert-secondary px-2 py-1">
                  {{request.lat_lanTo && request.lat_lanTo.lat != "" && request.lat_lanTo.lan != ""
                  ? request.lat_lanTo.lat + "," + request.lat_lanTo.lan
                  : "/" }}
                </p>
              </div>
            </div>
          </div>
          <div class="row">
            <div class="col">
              <div class="form-group my-2">
                <label>Wilaya -arrivée- *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.wilayaTo || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Baladia -arrivée- *:</label>
                <p class="alert alert-secondary px-2 py-1">{{request.baladiaTo || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Position GPS -arrivée- *:</label>
                <p class="alert alert-secondary px-2 py-1">
                  {{request.lat_lanTo && request.lat_lanTo.lat != "" && request.lat_lanTo.lan != ""
                  ? request.lat_lanTo.lat + "," + request.lat_lanTo.lan
