          <hr class="my-1" />
          <div class="row">
            <div class="col">
              <div class="form-group my-2">
                <label>Nom du destinataire *:</label>
                <p class="alert alert-secondary px-2 py-1">{{ request.arriveName || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Téléphone du destinataire *:</label>
                <p class="alert alert-secondary px-2 py-1">{{ request.arriveNumber || "/" }}</p>
              </div>
            </div>
          </div>
          <hr class="my-1" />
          <div class="row">
            <div class="col">
              <div class="form-group my-2">
                <label>Téléphone du Transporteur *:</label>
                <p class="alert alert-secondary px-2 py-1">{{ request.driverPhone || "/" }}</p>
              </div>
            </div>
            <div class="w-100"></div>
            <div class="col">
              <div class="form-group my-2">
                <label>Code du Paquet *:</label>
                <p class="alert alert-secondary px-2 py-1">{{ request.packageCode || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Code de ramassage:</label>
                <p class="alert alert-secondary px-2 py-1">{{ request.pickupCode || "/" }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Code secret:</label>
                <p class="alert alert-secondary px-2 py-1">{{ request.secretCode || "/" }}</p>
              </div>
            </div>
          </div>
          <hr class="my-1" />
          <div class="row">
            <div class="col">
              <div class="form-group my-2">
                <label>Date de Mise en Ligne *:</label>
                <p
                  class="alert alert-secondary px-2 py-1"
                >{{ (request.createDate || "/") | moment }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Date de Livraison:</label>
                <p
                  class="alert alert-secondary px-2 py-1"
                >{{ (request.deliveryTime || "/") | moment }}</p>
              </div>
            </div>
            <div class="col">
              <div class="form-group my-2">
                <label>Statut *:</label>
                <p
                  class="alert alert-secondary px-2 py-1"
                >{{ (request.state || "/") | state_filter }}</p>
              </div>
            </div>
          </div>
          <!--  /form -->
          <div class="card card-body text-center p-0" v-if="show_map">
            <div id="map" style="width:100%;height:400px;"></div>
          </div>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
import { db, dbRT } from "../firebase";
import { Loader } from "@googlemaps/js-api-loader";

export default {
  name: "packageTrackComponent",
  components: {},
  data() {
    return {
      request: null,
      packageCode: "W271021-67211",
      driver: null,
      show_map: false,
      isLoading: false,
      errorMessage: null
    };
  },
  watch: {},
  created() {
    //this.show_google_maps({ lan: "10.5775", lat: "10.3615" });
  },
  methods: {
    tray_again() {
      this.isLoading = false;
      this.errorMessage = null;
      this.findPackage(this.id);
    },
    syncState(phone) {
      phone = "+213793470277";
      if (!phone) return;
      dbRT.ref("users/" + phone).on("value", snapshot => {
        console.log("users/" + this.$store.state.profile.phone, snapshot);
        if (!snapshot.exists()) {
          return;
        }
        if (
          (snapshot.val().mode || "off") == "on" &&
          snapshot.val().driverLat &&
          snapshot.val().driverLng
        )
          setTimeout(
            () =>
              this.show_google_maps(
                snapshot.val().driverLat,
                snapshot.val().driverLng
              ),
            10
          );
      });
    },
    findPackage() {
      console.log("packageCode", this.packageCode);
      this.isLoading = true;
      this.errorMessage = null;
      db.collection("requests")
        .where("packageCode", "==", this.packageCode)
        .limit(1)
        .get()
        .then(querySnapshot => {
          this.isLoading = false;
          this.errorMessage = null;
          console.log("Documents loaded!", querySnapshot);
          let doc = querySnapshot.docs[0];
          if (doc) {
            this.request = { id: doc.id, ...doc.data() };
            this.request.lat_lanFrom = {
              lat: doc.data().startLat,
              lan: doc.data().startLng
            };
            this.request.lat_lanTo = {
              lat: doc.data().endLat,
              lan: doc.data().endLng
            };
            //
            console.log("Document loaded", doc.id, doc.data());
            this.$swal("Paquet trouvé : " + this.packageCode);
            if (doc.data().driverPhone)
              setTimeout(() => this.syncState(doc.data().driverPhone), 10);
          } else {
            this.$swal("Pas de Paquets avev le code " + this.packageCode);
          }
        })
        .catch(error => {
          this.isLoading = false;
          this.errorMessage = null;
          console.log("error", error);
        });
    },
    show_google_maps(driverLat, driverLng) {
      console.log("driverLat", driverLat);
      console.log("driverLng", driverLng);
      if (driverLat == "" || driverLng == "") return;
      this.show_map = true;
      const loader = new Loader({
        apiKey: this.$googleMapAPiKey,
        version: "weekly"
        //...additionalOptions,
      });
      loader.load().then(() => {
        new window.google.maps.Map(document.getElementById("map"), {
          center: new window.google.maps.LatLng(driverLat, driverLng),
          zoom: 15
        });
      });
    }
  }
};
</script>

<style >
.package-track {
  color: #000000;
}
</style>
