<template>
  <div>
    <div>
      <h2>Contact</h2>
      <p class="lead">WeeWee team est là pour vous aider</p>
      <div class="card card-body text-center text-warning" v-if="errorMessage">
        {{ errorMessage }}
        <hr />
        <button class="btn btn-block btn-warning" @click="tray_again">Réessayer</button>
      </div>
      <loading-compoenent v-else-if="isLoading" />
      <div class="card card-body text-center" v-else-if="isPosted">
        <div style="font-size: 4rem;">
          <i class="bi-check text-success me-2" />
        </div>Votre message a été envoyé
        <hr />
        <button class="btn btn-block btn-warning" @click="post_again">Envoyer à nouveau</button>
      </div>
      <div v-else>
        <form @submit.prevent="submit_document">
          <div class="form-group py-1">
            <input
              type="text"
              v-model="document.name"
              class="form-control"
              placeholder="Nom et prénom  *"
              required
            />
          </div>
          <div class="form-group py-1">
            <input type="email" v-model="document.email" class="form-control" placeholder="Email" />
          </div>
          <div class="form-group py-1">
            <input
              type="tel"
              v-model="document.phone"
              class="form-control"
              placeholder="Téléphone"
              required
            />
          </div>
          <div class="form-group py-1">
            <textarea
              v-model="document.msg"
              class="form-control"
              placeholder="Message *"
              required
              rows="5"
            ></textarea>
          </div>
          <div class="d-grid py-1">
            <button class="btn btn-primary btn-lg" type="submit">
              <i class="bi-envelope-open me-2" /> Envoyer
            </button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { db, FF_FieldValue } from "../firebase";
export default {
  name: "contactComponent",
  components: {},
  data() {
    return {
      document: {
        name: "",
        email: "",
        phone: "",
        msg: ""
      },
      errorMessage: null,
      isLoading: false,
      isPosted: false
    };
  },
  created() {
    this.reset_document();
  },
  methods: {
    tray_again() {
      this.isLoading = false;
      this.errorMessage = null;
    },
    post_again() {
      this.isLoading = false;
      this.errorMessage = null;
      this.isPosted = false;
      this.reset_document();
    },
    reset_document() {
      this.document.name = "";
      this.document.email = "";
      this.document.phone = "";
      this.document.msg = "";
    },
    submit_document() {
      this.post_document_firebase();
    } /*
    post_document() {
      this.isLoading = true;
      this.errorMessage = null;
      let form = {
        password: "no-password",
        collecion: "contact",
        document: JSON.stringify(this.document)
      };
      this.$http
        .post("http://localhost/weewee-web-apps/post-to-file/", form)
        .then(res => {
          this.isLoading = false;
          this.errorMessage = false;
          console.log(res);
          if (res.data.status) {
            this.isPosted = true;
            this.$swal("Message recu!");
          } else {
            this.$swal("Oaps, une erreur!");
          }
        })
        .catch(error => {
          this.isLoading = false;
          this.errorMessage = error;
          console.log(error);
        });
    },*/,
    post_document_firebase() {
      this.isLoading = true;
      this.errorMessage = null;
      let date = FF_FieldValue.serverTimestamp();
      let document = {
        ...this.document,
        created_at: date,
        modified_at: date
      };
      db.collection("contact-form")
        .add(document)
        .then(() => {
          this.isLoading = false;
          this.errorMessage = null;
          console.log("Document successfully created!");
          this.$swal("Message recu!");
          this.reset_document();
          this.isPosted = true;
          /*this.$router.push({
            name: "home"
          });*/
        })
        .catch(error => {
          this.isLoading = false;
          this.errorMessage = error;
          console.log("error", error);
        });
    }
  }
};
</script>

