<template>
  <div class="container px-4 pt-4 package-track">
    <form @submit.prevent="getToken">
      <div class="card card-body">
        <div class="input-group">
          <input
            type="text"
            v-model="vapidKey"
            class="form-control text-center"
            placeholder="vapidKey"
          />
          <button class="btn btn-outline-primary btn-lg" type="submit">
            <i class="bi-bell me-2" /> Obtenir le token FCM
          </button>
        </div>
        <input
          type="text"
          v-model="fcm"
          class="form-control text-center mt-2"
          placeholder="FCM Token"
        />
      </div>
    </form>
  </div>
</template>

<script>
import { db, messaging } from "../firebase";
export default {
  name: "",
  components: {},
  data() {
    return {
      vapidKey:
        "BOWiLlYUUtKQ1NSGYkZZ5kwWiPJH7i2j20NJe_NDPVVc9aQwbL2coHtX0tOeL5bO1RYpak2QTvA8LmlHbK-EKKU",
      fcm: "",
      //
      isLoading: false,
      errorMessage: null
    };
  },
  watch: {},
  created() {},
  methods: {
    getToken() {
      console.log("Getting Token");
      messaging
        .getToken({ vapidKey: this.vapidKey })
        .then(currentToken => {
          if (currentToken) {
            console.log("currentToken", currentToken);
            this.fcm = currentToken;
            this.requested = true;
            this.updateFCM();
          } else {
            this.fcm = "";
            console.log("No registration token available.");
          }
        })
        .catch(err => {
          this.fcm = "";
          console.log("An error occurred while retrieving token.", err);
        });
    },
    updateFCM() {
      console.log(
        "Update FCM",
        this.$store.state.profile
          ? this.$store.state.profile.phone
          : "no-phone",
        this.fcm
      );
      if (
        this.$store.state.profile &&
        this.$store.state.profile.phone &&
        this.fcm
      )
        db.collection("users")
          .doc(this.$store.state.profile.phone)
          .update({ fcm: this.fcm })
          .then(() => {
            console.log("Document updated!");
          })
          .catch(error => {
            console.log("error", error);
          });
    }
  }
};
</script>

<style >
.package-track {
  color: #000000;
}
</style>
